import { Input } from '@angular/core';
import { ConfigurationService } from '../../common/configuration.service';
import { SettingsComponent } from '../../common/settings.component';
import { Border } from '../../common/property-types';

/**
 * @hidden
 */
export abstract class YAxisNotesLabelComponentGenerated extends SettingsComponent {
  /**
   * The background color of the label. Accepts a valid CSS color string, including hex and rgb.
   */
  @Input() public background: string;

  /**
   * The border of the label.
   */
  @Input() public border: Border;

  /**
   * The text color of the label. Accepts a valid CSS color string, including hex and rgb.
   */
  @Input() public color: string;

  /**
   * The template which renders the labels.
   *
   * The field which can be used in the template is `value`. It defines the axis value.
   *
   * The text can be split into multiple lines by using line feed characters (`"\n"`).
   */
  @Input() public content: (e: any) => string;

  /**
   * The font style of the label.
   */
  @Input() public font: string;

  /**
   * The format used to display the notes label. Uses `kendo.format`. Contains one placeholder (`"{0}"`)
   * which represents the axis value.
   */
  @Input() public format: string;

  /**
   * The position of the labels.
   *
   * - `"inside"`&mdash;The label is positioned inside the icon.
   * - `"outside"`&mdash;The label is positioned outside the icon.
   */
  @Input() public position: string;

  /**
   * The rotation angle of the label. By default, the label is not rotated.
   */
  @Input() public rotation: number;

  /**
   * If set to `true`, the Chart displays the y-axis notes label. By default, the y-axis notes label is
   * visible.
   */
  @Input() public visible: boolean;

  constructor(public configurationService: ConfigurationService) {
    super('notes.label', configurationService);
  }
}



